.organizerContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.organizerInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    justify-content: space-between;
}

.organizerLogo {
    width: 50%;
}

.organizerDescription {
    width: 45%;
    font-weight: 500;
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
    text-align: left;
    color: white;
}


@media screen and (max-width: 600px) {
    .organizerContainer {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .organizerInfo {
        flex-direction: column;
    }

    .organizerLogo {
        width: 70vw;
    }

    .organizerDescription {
        font-size: 1rem;
        width: 70vw;
    }

}